import { FC, useContext } from "react"
import styles from "./index.module.scss"
import Nav from "../Nav"
import Forms from "../Forms"
import { AppContext } from "../../contexts/AppContext"
import Form56 from "../../froms/Form56"
import { flushSync } from "react-dom"
import fieldStyles from "../Field/index.module.scss"

const Layout: FC = () => {
  const { setFormInfo, setForms, setA4 } = useContext(AppContext)

  return (
    <div
      className={styles.layout}
      onDragOver={(e) => e.preventDefault()}
      onDrop={(e) => {
        e.preventDefault()

        const file = e.dataTransfer.files[0]
        const reader = new FileReader()
        reader.onload = () => {
          const content = String(reader.result)

          try {
            const fileJson = JSON.parse(
              content?.match(/<script>(.*)<\/script>/)?.[1] ?? "null"
            ) as {
              forms: string[]
              data: string[]
            } | null
            if (!fileJson) throw new Error("檔案格式錯誤")

            setFormInfo({
              label: "離線檔案",
              saveAsName: "",
            })
            const fileForms = fileJson.forms.map((x, i, array) => {
              switch (x) {
                case "Form56":
                  const form56: FC = Form56.bind(null)
                  form56.displayName = `${Form56.displayName}.${
                    Date.now() - array.length + i
                  }`
                  return form56
                default:
                  throw new Error("不支援的表格類型")
              }
            })
            setForms(fileForms)
            flushSync(() => {
              setTimeout(() => {
                const domFields = Array.from(
                  document.getElementsByClassName(fieldStyles.field)
                )
                fileJson.data.forEach((value, i) => {
                  ;(domFields[i] as HTMLSpanElement).innerHTML = value
                })
              }, 0)
            })
          } catch (e) {
            alert(e)
          }
        }
        reader.readAsText(file)
      }}
    >
      <Nav />
      <div className={styles.container}>
        <div className={styles.a4} ref={setA4}>
          <Forms />
        </div>
      </div>
    </div>
  )
}

export default Layout
